.wrap-main-nav {
    max-width: 1792px;
    margin: 0 auto;
    margin-bottom: 40px;

    .wrap {
        border-bottom: 1px solid #000000;
        position: relative;
        height: 80px;
        align-items: center;
    }

    .back-vne {
        position: absolute;
        left: 0;
        font: 600 16px var(--font1);
        color: #333333;

        svg {
            width: 37px;
            height: 37px;
            margin-right: 5px;
            position: relative;
            transform: rotate(180deg);
        }

        .dotted-svg {
            &::before {
                left: -5px;
            }
        }
    }

    .menu {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;

        li {
            margin: 0 25px;

            a {
                color: #191919;
                font-size: 16px;
                font-family: var(--font1);
                position: relative;
            }

            &.active {
                a {
                    color: var(--color-main);
                    font-weight: 600;

                    &::before {
                        content: "";
                        background: var(--color-main);
                        height: 2px;
                        left: 10px;
                        right: 10px;
                        position: absolute;
                        bottom: -10px;
                    }
                }
            }
        }
    }
}