.section-frame1 {
    max-width: 1792px;
    margin: 0 auto;
    margin-bottom: 30px;

    .box-content {
        background: #F2EFE7;
        padding: 170px 126px 100px 343px;
        width: calc(100% - 880px);
        position: relative;
        font-family: var(--font1);

        &::before {
            content: "";
            background: url(images/graphics/bg-hanhtrinh.png) no-repeat;
            height: 89px;
            position: absolute;
            width: 900px;
            top: 0;
            left: 0;
        }

        label {
            color: #191919;
            font-size: 14px;
            line-height: 115%;
            letter-spacing: 0.24em;
            margin-bottom: 23px;
            display: block;
        }

        .title {
            color: #191919;
            font-size: 64px;
            margin-bottom: 28px;
            line-height: 1.2;
        }

        p {
            color: #6B6B6B;
            font-size: 16px;
            line-height: 160%;
            margin-bottom: 28px;
            font-family: Arial;
        }
    }

    .box-img {
        width: 880px;
        position: relative;

        img {
            width: 100%;
        }


        .swiper-slide {
            &::before {
                content: "";
                height: 80px;
                background: #fff;
                position: absolute;
                bottom: 0;
                width: 100%;
                z-index: 2;
            }

            h3 {
                position: absolute;
                left: 40px;
                bottom: 25px;
                font-size: 16px;
                color: #6B6B6B;
                font-family: Arial;
                z-index: 3;
            }
        }

        .swiper-pagination {
            font-family: var(--font1);
            color: #AA7D54;
            font-size: 24px;
            width: 80px;
            right: 200px;
            left: inherit;

            .swiper-pagination-current {
                font-size: 40px;
                color: #AA7D54;
            }

            .swiper-pagination-total {
                font-size: 25px;
                color: #AA7D54;
                opacity: 0.5;
            }
        }

        .swiper-button-prev,
        .swiper-button-next {
            width: 80px;
            height: 80px;
            line-height: 80px;
            bottom: 0;
            top: inherit;
            background: #AA7D54;

            svg {
                height: 34px;
                width: 34px;
                color: #fff;
            }
        }

        .swiper-button-prev {
            right: 80px;
            left: inherit;
            transform: rotate(180deg);
        }

        .swiper-button-prev.swiper-button-disabled,
        .swiper-button-next.swiper-button-disabled {
            opacity: 1;
            background: #F2EFE7;

            svg {
                color: #AA7D54
            }
        }
    }
}

.section-frame2 {
    margin-bottom: 50px;

    .item-news {
        width: 440px;
        border: 1px solid #191919;
        margin: 0 40px;
        margin-bottom: 40px;
        margin-top: 60px;
        float: left;

        &::before {
            content: "";
            border: 1px solid #AA7D54;
            position: absolute;
            top: 10px;
            bottom: -10px;
            right: -10px;
            left: 10px;
        }

        .wrap {
            background: #fff;
            padding: 18px;
            position: relative
        }

        .thumb-art {
            width: 110px;
            margin-right: 26px;
            position: relative;

            &::before {
                content: "";
                background: url(images/graphics/book.png);
                background-size: cover;
                position: absolute;
                top: -5px;
                right: -10px;
                bottom: -5px;
                left: -5px;
            }
        }

        .description {
            font-size: 14px;
        }

        .line {
            background: #E7D5C5;
            height: 130px;
            width: 1px;
            position: absolute;
            bottom: -160px;
            left: 100px;

            &::after {
                content: "";
                background: #E7D5C5;
                height: 14px;
                width: 14px;
                position: absolute;
                border-radius: 50%;
                top: -4px;
                left: -6.5px;
            }

            &::before {
                content: "";
                background: url(images/graphics/bong.png);
                background-size: contain;
                width: 171px;
                height: 15px;
                position: absolute;
                bottom: -10px;
                left: -85px;
            }
        }

        &.active {
            width: 480px;
            margin-top: 0;

            .line {
                height: 232px;
                bottom: -260px;

                &::before {
                    width: 361px;
                    height: 27px;
                    position: absolute;
                    bottom: -10px;
                    left: -180px;
                }
            }

            .thumb-art {
                width: 147px;
            }
        }
    }
}

.section-frame3 {
    max-width: 1792px;
    margin: 0 auto;
    margin-bottom: 80px;
    position: relative;

    .container {
        padding: 0 80px;
    }

    .time-line {
        background: url(images/graphics/time-line.png) no-repeat;
        height: 4px;
        position: relative;
        background-size: cover;
        margin-bottom: -70px;
        margin-top: 120px;
    }

    .swiper-container {
        padding-top: 110px;
    }

    .swiper-slide {
        padding-left: 30px;
        position: relative;
        padding-bottom: 20px;

        &::before {
            content: "";
            border-left: 1px dashed #AA7D54;
            height: 100%;
            position: absolute;
            left: 9px;
        }

        .year {
            color: #AA7D54;
            font-size: 32px;
            font-family: var(--font1);
            position: absolute;
            top: -120px;
            left: 0;

            &::before {
                content: "";
                width: 20px;
                height: 20px;
                background: #AA7D54;
                border-radius: 50%;
                position: absolute;
                bottom: -39px;
                left: 7px;
            }

            &::after {
                content: "";
                width: 32px;
                height: 32px;
                position: absolute;
                border: 1px solid #AA7D54;
                border-radius: 50%;
                bottom: -45px;
                left: 0;
            }
        }
    }

    .swiper-button-prev,
    .swiper-button-next {
        width: 62px;
        height: 48px;
        line-height: 48px;
        background: none;
        top: 0;
    }

    .swiper-button-prev {
        transform: rotate(180deg);
    }
}

.section-frame4 {
    max-width: 1792px;
    margin: 0 auto;
    background: #F2EFE7 url(images/graphics/bg-camon.png) no-repeat bottom right;
    padding: 80px 0;
    position: relative;
    margin-bottom: 60px;

    .list_comment {
        width: calc(100% - 370px);
    }

    .form_dk {
        width: 370px;
        padding-left: 60px;

        h3 {
            font: 600 20px var(--font1);
            color: #191919;
            margin-bottom: 15px;
        }
    }

    .head {
        margin-bottom: 30px;

        .loi-chuc {
            border: 1px solid #AA7D54;
            display: inline-block;
            margin-bottom: 30px;
            color: #191919;
            font-weight: bold;
            font-size: 20px;
            position: relative;

            &>.flex {
                position: relative;
                background: #F2EFE7;
            }

            &::before {
                content: "";
                border: 1px solid #D3BF9F;
                position: absolute;
                top: 10px;
                bottom: -7px;
                right: -7px;
                left: 7px;
            }

            .text {
                color: #AA7D54;
                font-size: 20px;
                font-weight: 400;
                padding: 12px;
                border-right: 1px solid #AA7D54;

                svg {
                    width: 35px;
                    height: 35px;
                    color: #AA7D54;
                    margin-left: 6px;
                }
            }

            .number {
                padding: 12px;
            }
        }

        .img {
            margin-top: -20px;
        }
    }
}

.section-frame5 {
    .item-news {
        display: flex;
        flex-wrap: wrap;
        color: #D4D4D4;
        font-size: 16px;

        .thumb-art {
            width: 67%;
        }

        .content {
            width: 33%;
            background: #070707 url(images/graphics/bg-tacpham.png) no-repeat bottom;
            padding: 65px;
        }

        .title-news {
            font: 400 48px var(--font1);
            margin-bottom: 30px;

            a {
                color: #D3BF9F;
            }
        }

        .description {
            color: #D4D4D4;

            p {
                margin-bottom: 20px;
            }

            .flex {
                justify-content: space-between;
                color: #D3BF9F;

                svg {
                    color: #D3BF9F;
                    width: 24px;
                    height: 24px;
                }
            }
        }

        .btn-view {
            border: 1px solid #AA7D54;
            color: #AA7D54;
            padding: 7px 20px;
            display: inline-block;
            margin-top: 60px;

            svg {
                margin-left: 6px;
                color: #AA7D54;
                width: 22px;
                height: 22px;
            }
        }
    }

    .slide-img {
        position: relative;
    }

    .swiper-button-prev,
    .swiper-button-next {
        background: #191919;
        width: 40px;
        height: 40px;
        line-height: 40px;
        right: 33%;
        margin-right: 24px;
        top: inherit;
        bottom: 24px;

        svg {
            color: #fff
        }
    }

    .swiper-button-prev {
        bottom: 74px;
        left: inherit;
        transform: rotate(180deg);
    }

    .swiper-button-disabled {
        opacity: 0.6;
    }
}

.section-frame6 {
    padding: 100px 0 270px 0;
    position: relative;
    overflow: hidden;

    &::after {
        content: "";
        background: url(images/graphics/bg-chuyennghe.png) no-repeat;
        background-size: contain;
        position: absolute;
        top: -300px;
        height: 400px;
        width: 1600px;
        right: -20%;
    }

    &::before {
        content: "";
        background: url(images/graphics/bg-chuyennghe2.png) no-repeat;
        background-size: contain;
        position: absolute;
        bottom: -100px;
        height: 249px;
        width: 1144px;
        left: 20%;
    }
}

.list-chuyen-nghe {
    .gird {
        margin: 0 -15px;
    }

    .item {
        width: 33.33%;
        margin-bottom: 30px;
        padding: 0 15px;

        .item-news {
            background: #fff;
            padding: 16px;

            .thumb-art {
                float: right;
                width: 74px;
                height: 74px;
                margin-left: 15px;
            }

            .description {
                margin-bottom: 40px;
            }

            .author {
                color: #AA7D54;
            }
        }
    }
}